* {
   box-sizing: border-box;
}

body {
   margin: 0;
   background-color: #e14647;
}

h2 {
   background-color: #e14647;
   padding: 18px;
   color: #fff;
}

.App {
   padding: 30px;
   background-color: #fff;
   width: 80%;
   margin: 50px auto;
   box-shadow: 1px 2px 15px #746666;
}

input {
   width: 80%;
   border: 0;
   padding: 6px 10px;
   background-color: #e8e8e8;
}

button {
   width: 20%;
   border: 0;
   padding: 6px 0;
   margin: 0;
   outline: 0;
   color: #fff;
   font-weight: bold;
   cursor: pointer;
   background-color: #4dc55b;
}

ul {
   list-style: none;
   padding: 0;
}

li {
   margin: 15px 0;
}

li span {
   display: inline-block;
   width: 60%;
   color: #6a6a6a;
}

li > button:first-of-type {
   background-color: #7f8f91;
   color: #fff;
   margin-right: 1%;
   width: 19%;
}

li > button:last-of-type {
   background-color: #d9534f;
   color: #fff;
}

ul > form {
   margin: 15px 0;
}
